import { mapState } from "vuex";
import { deleteArticle } from "@/api/article"
export default {
  name: "article-meta",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    async delArticle(slug) {
      await deleteArticle(slug)
      this.$router.go(-1)
    }
  }
};
